.header {
  background-image: url(../images/photo/Cover_photo.png);
  background-position: center;
  background-size: cover;
 
  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
    padding: $headerTopPadding 0 180px;
    box-sizing: border-box;

  }

  &__title {
    line-height: 100%;
    color: #ffffff;
    letter-spacing: -1.5px;
    text-align: center;
    transition: 0.3s;
    font-weight: 700;
    font-size: 36px;
    line-height: 100%;
    align-items: center;
    text-align: center;
    letter-spacing: -1.5px;
    color: #FFFFFF;

    @include onDesktop {
      font-weight: 700;
      font-size: 80px;
      line-height: 100%;
    }

  }

  &__call {
    
    transition: 0.3s;
    text-decoration: none;


    @include onDesktop {
      &::before {
        content: "+1 234 555-55-55";
        position: relative;
        top: 50%;
        transform: translate(-100%, -50%);
        color: #fff;
        display: block;
        width: max-content;
        padding-right: 20px;
        font-weight: 700;
        font-size: 12px;
        line-height: 16px;
        text-transform: uppercase;
        letter-spacing: 2px;
        opacity: 0;
        transition: 0.3s;
        pointer-events: none;

      }

      &:hover::before {
        opacity: 1;
        pointer-events: all;
      }
    }

  }

  &__block {
    display: flex;
    align-items: center;
    gap: 30px;

    @include onDesktop {
      gap: 40px;
    }
  }
  
}
